.sidebar {
    display: flex;
    flex-direction: column;
    flex:0.35; 
}
/* we are using only 35% of the screen */

.sidebar__header{
    display:flex;  
    justify-content: space-between;
    padding: 20px;
    border-right: 1px solid lightgray;
}

.sidebar__search{
    display:flex;  
    align-items: center;
    background-color: #f6f6f6;
    padding: 10px;
    height:39px;
}

.sidebar__searchContainer{
    display:flex;  
    align-items: center;
    background-color: white;
    width: 100%;
    height:35px;
    border-radius: 20px;
}
/* gives gray color to the search icon */
.sidebar__searchContainer > input {
    border: none;
    margin-left: 10px;
}

.sidebar__searchContainer > .MuiSvgIcon-root{
    color:gray;
}

.sidebar__headerRight{
    display:flex;  
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
}
/* space the icons in the right container */
.sidebar__headerRight > .MuiSvgIcon-root{
    margin-right: 2vw;
    font-size: 24px !important;
}

.sidebar__chats {
flex:1;
background-color: white;
overflow: scroll;
}